.footer__list{display:flex}.footer__list{flex-direction:column}.footer{padding-top:80px;background-color:#151823;color:#666873}.footer__container{padding:0 1.5rem}.footer__top{display:grid;grid-template-columns:repeat(auto-fit, minmax(20rem, 1fr));grid-row-gap:2rem;row-gap:2rem}.footer__title{font-size:1.8rem;color:#fff;font-weight:500;margin-bottom:2rem}.footer__list{grid-gap:2rem;gap:2rem}.footer__list-item{list-style:none}.footer__list-link{text-decoration:none;color:inherit;transition:color .25s}.footer__list-link:hover{color:#fff}.footer__bottom{padding:20px 0;padding-top:50px}
.header__container,.header__nav .nav__list{display:flex}.header__nav .nav__list{align-items:center}.header__container{justify-content:space-between}.header__container{align-items:center}.header{background-color:rgba(255,255,255,.9);-webkit-backdrop-filter:blur(5px);backdrop-filter:blur(5px);padding:15px 0;position:fixed;width:100%;top:0;left:0;z-index:999}@media only screen and (max-width: 600px){.header{padding:15px 0}}.header .flag{flex-shrink:0;height:50px}@media only screen and (max-width: 768px){.header .flag{height:40px}}.header .flag .flag__img{width:100%;height:100%;object-fit:contain;object-position:center}.header__nav{flex-grow:1}@media only screen and (max-width: 600px){.header__nav .nav{display:none}}.header__nav .nav__list{justify-content:flex-end;grid-gap:30px;gap:30px}.header__nav .nav__item{position:relative;display:inline-block}.header__nav .nav__item::before{content:"";position:absolute;left:0;bottom:-3px;width:100%;height:2px;background-color:#000;border-radius:4px;scale:0;-webkit-transform-origin:right;transform-origin:right;transition:scale .25s}.header__nav .nav__item:hover::before{scale:1;-webkit-transform-origin:left;transform-origin:left}.header__nav .nav__item .is-active{color:#0561ee}.header__nav .nav__link{font-size:18px;font-weight:500}.header__nav .burger{width:50px;height:50px;position:fixed;top:10px;right:20px;border-radius:4px;z-index:10}@media only screen and (min-width: 601px){.header__nav .burger{display:none}}.header__nav .burger span{position:relative;margin-top:9px;margin-bottom:9px;-webkit-user-select:none;user-select:none;position:absolute;top:50%;left:50%;margin-left:-15px;margin-top:-1.5px}.header__nav .burger span,.header__nav .burger span::before,.header__nav .burger span::after{display:block;width:30px;height:3px;background-color:#2a2a2a;outline:1px solid rgba(0,0,0,0);transition-property:background-color,-webkit-transform;transition-property:background-color,transform;transition-property:background-color,transform,-webkit-transform;transition-duration:.3s}.header__nav .burger span::before,.header__nav .burger span::after{position:absolute;content:""}.header__nav .burger span::before{top:-9px}.header__nav .burger span::after{top:9px}.header__nav .burger.clicked span{background-color:rgba(0,0,0,0)}.header__nav .burger.clicked span::before{-webkit-transform:translateY(9px) rotate(45deg);transform:translateY(9px) rotate(45deg)}.header__nav .burger.clicked span::after{-webkit-transform:translateY(-9px) rotate(-45deg);transform:translateY(-9px) rotate(-45deg)}.header__nav .burger.clicked span:before,.header__nav .burger.clicked span:after{background-color:#fff}.header__nav .burger:hover{cursor:pointer}.header__nav .burger-nav{background-color:#2a2a2a;position:fixed;z-index:9;top:0;right:0;height:100vh;max-width:450px;width:100%;padding:100px 40px 60px 50px;overflow-y:auto;scale:0;-webkit-transform-origin:top right;transform-origin:top right;border-radius:0 0 0 100%;transition:all .5s ease}@media only screen and (min-width: 601px){.header__nav .burger-nav{display:none}}.header__nav .burger-nav .burger-nav__item{text-align:center;opacity:0;-webkit-transform:translateX(100px);transform:translateX(100px);transition:all 1s ease}.header__nav .burger-nav .burger-nav__item:not(:last-child){margin-bottom:40px}.header__nav .burger-nav .burger-nav__link{color:#fff;font-size:30px;font-weight:700}.header__nav .burger-nav.show{border-radius:0;scale:1;-webkit-transform-origin:top right;transform-origin:top right}.header__nav .burger-nav.show .burger-nav__item{opacity:1;-webkit-transform:translateX(0);transform:translateX(0)}.header__nav .burger-nav__link.burger-nav__is-active{color:#0561ee}
